body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.wrapper {
    background-image: url(data:image/png;base64,);
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.main {
    height: 90vh;
    background: #fff;
    box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
    width: 80vw;
    margin: 0 auto;
    display: block;
}
.row{
    display: flex;
    flex-direction: row;
}
.title-container {
    height: 90vh;
    background: url(/WeatherApp/static/media/bg.b6796c73.jpg) center center no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: #000;
    width: 40vw;
}

.title-container__title {
    font-size: 30px;
    letter-spacing: 2px;
    line-height: 1.3;
    font-family: 'Ubuntu', sans-serif;
}

.title-container__subtitle {
    font-style: italic;
    font-weight: 100;
    letter-spacing: 1px;
    line-height: 1.5;
    font-family: 'Ubuntu', sans-serif;
}

.form-container {
    background-color: #2c3e50;
    height: 85vh;
    padding-top: 5vh;
    padding-left: 25px;
    /*right: 25px;*/
    /*width: 40vw;*/
}

input[type="text"] {
    background-color: transparent;
    border: 0;
    color: white;
    border-bottom: solid 1px #f16051;
    width: 30%;
    padding-bottom: 4px;
    font-weight: lighter;
    letter-spacing: 2px;
    margin-bottom: 30px;
    margin-right: 20px;
    font-size: 20px;
}

input[type="text"] {
    outline: none;
}

input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
    -webkit-text-fill-color: #fff !important;
}

button {
    border: 0;
    padding: 8px 20px;
    border-radius: 2px;
    letter-spacing: 1px;
    font-size: 15px;
    cursor: pointer;
    background-color: #f16051;
    color: #fff;
    font-weight: 100;
    height: 35px;

}

button:active {
    outline: none;
}

.weather__info {
    width: 40vw;
    font-size: 15px;
    font-weight: 200;
    letter-spacing: 2px;
    height: 70vh;
}

.weather__key {
    color: #f16051;
    border-bottom: solid 2px rgba(255,255,255,0.06);
    padding: 20px 0 20px 0;
    font-weight: 400;
}

.weather__key:last-child {
    border: 0;
}

.weather__value {
    color: #fff;
    font-weight: 200;
    font-size: 15px
}

.weather__error {
    color: #f16051;
    font-size: 20px;
    letter-spacing: 1px;
    font-weight: 200;
}
.form {
    display: flex;
    justify-content: left;
    width: 40vw;
}
.gps{
    margin-right: 5px;
}
.map{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 90vh;
    width: 80vw;
    position: relative;
}
.onMap{
    position: absolute;
    top: 0;
    right: 0;
    z-index: 999;
}
.hidden{
    display: none;
}
span{
    font-size: 12px;
}
